'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { LayoutDashboard, Stethoscope, Users, LogOut } from 'lucide-react';

export default function AdminSidebar() {
  const pathname = usePathname();

  const menu = [
    { name: 'Genel Bakış', href: '/admin/dashboard', icon: LayoutDashboard },
    { name: 'Doktorlar', href: '/admin/dashboard/doktorlar', icon: Stethoscope },
    { name: 'Hastalar', href: '/admin/dashboard/hastalar', icon: Users },
  ];

  return (
    <aside className="w-64 bg-[#1e1e2d] text-white min-h-screen flex flex-col shrink-0 shadow-xl">
      <div className="h-20 flex items-center px-8 border-b border-gray-800 shrink-0">
        <span className="text-xl font-bold tracking-tight">PAN<span className="text-red-600">ASE</span></span>
      </div>
      
      <nav className="flex-1 py-6 px-4 space-y-1">
        {menu.map((item) => (
          <Link
            key={item.href}
            href={item.href}
            className={`flex items-center px-4 py-3 rounded-xl transition-all ${
              pathname === item.href ? 'bg-red-600 text-white shadow-lg' : 'text-gray-400 hover:bg-gray-800'
            }`}
          >
            <item.icon className="w-5 h-5 mr-3" />
            <span className="text-sm font-medium">{item.name}</span>
          </Link>
        ))}
      </nav>

      <div className="p-4 border-t border-gray-800">
        <button 
          onClick={() => { localStorage.clear(); window.location.href = '/auth/login'; }}
          className="flex items-center w-full px-4 py-3 text-gray-400 hover:text-red-500 rounded-xl"
        >
          <LogOut className="w-5 h-5 mr-3" />
          <span className="text-sm">Çıkış</span>
        </button>
      </div>
    </aside>
  );
}